import React, { useState } from 'react'

import './Switcher.sass'

import { LOCALES } from './locales'

import UaFlag  from "~IMG/svg/Flags/icons8-ukraine-48.png"
import PlFlag  from "~IMG/svg/Flags/icons8-poland-48.png"
import UsaFlag from "~IMG/svg/Flags/icons8-usa-48.png"


//

const Select_lang = ({ handleChange, currentLocale }) => {

//

    const languages = [
        { name: 'English', code: LOCALES.ENGLISH },
        { name: 'Russian', code: LOCALES.RUSSIAN  },
        { name: 'Ukraine', code: LOCALES.UKRAINE }
    ]

    console.log('currentLocale', currentLocale)

//

    return (
        <div className='switcher'>
            
            {/* Drop down language setings. */}

            <span>Lang</span>

            <div>
                {
                    currentLocale === 'uk-UA'
                        ? <img src={UaFlag} ></img>
                        : currentLocale === 'ru-RU' 
                            ? ''
                            : <img src={UsaFlag} ></img>
                }
            </div>

            <select onChange={handleChange} value={currentLocale} >
                {languages.map(({ name, code }) => (
                    <option key={code} value={code}>
                        {name}
                    </option>
                ))}
            </select>

        </div>
    )

}





export default Select_lang